// Parameters should be boolean 
// True parameters form navitem with :not(.disabled) selector
@mixin sidebar-nav-item($not-disabled-content: true) {
    .navbar-nav {
        .nav-item {
            .nav-link {
                @if $not-disabled-content == true {
                    &:not(.disabled) {
                        @content;
                    }
                } @else {
                    @content;
                }
                
            }
        }
    }
}
@mixin sidebar-nav-item-varients($padding-left: false,$item-padding: .825rem 1rem) {
    .navbar-nav {
        .nav-item {
            .nav-link {
                padding: $item-padding;
                @if $padding-left == true {
                    padding-left: 1.25rem;
                }
                &:not(.disabled) {
                    @content;
                }
            }
        }
    }
}

@mixin sidebar-item-color-varients($color, $bg-color) {
    @content;
    background-color: $bg-color;
    color: $bg-color;
    @if $enable-shadows == true {
        box-shadow: $navbar-vertical-active-shadow;
    }
    &:hover {
        @if $enable-shadows == true {
            box-shadow: $navbar-vertical-hover-shadow;
        }
    }
}


@mixin sidebar-item-border-left-varients($color, $bg-color, $content: '') {
    @content;
    color: $bg-color;
    position: relative;
    &:before{
        content: $content;
        position: absolute;
        left: 0;
        height: 60%;
        background: $primary;
        width: 4px;
        border-top-right-radius: 4px;
        border-bottom-right-radius: 4px;
        box-shadow: 0 0 16px;
    }
    &:hover {
        color: $bg-color
    }
}

@mixin sidebar-nav-item-hover($bg-color) {
    color: $bg-color;
    .collapsed+.sub-nav {
        @content;   
    }
}

@mixin sidebar-nav-item-start-spaceing($padding: $spacer) {
    .navbar-nav{
        .nav-item {
            &:not(.static-item) {
                padding-left:$spacer;
                @content;
            }
        }
    }
}